@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
#root {
  min-height: 100vh;
  display: flex;
}
body {
  background-color: rgb(211, 211, 211);
  font-family: 'Roboto', sans-serif;

  margin: 0px;
  padding: 0px;
}
.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100%;
  flex: 1;
}
h1 {
  margin: 1rem 0;
}
.container {
  width: 600px;
  flex-grow: 1;
  height: 100%;
  background-color: white;
  border-radius: 1rem;
  margin: 2rem 0;
  padding: 1rem;
}
